<template>
  <div class="share">
    <ul  class="content" v-infinite-scroll="loadMore" infinite-scroll-disabled="moreLoading" infinite-scroll-distance="0" infinite-scroll-immediate-check="false">
      <li v-for="item in list">
        <section class="content_l">
          <h2>{{item.nickname}}</h2>
          <!--<span>{{item.type | typeFilter}}</span>-->
        </section>
        <section class="content_r">
          <h2>+{{item.money}}</h2>
          <span>{{item.create_time | timeFilter}}</span>
        </section>
      </li>
      <!--底部判断是加载图标还是提示“全部加载”-->
      <li class="more_loading" v-if="!queryLoading && list.length !== 0">
        <mt-spinner type="fading-circle"  :size="25" v-show="moreLoading&&!allLoaded"></mt-spinner>
        <span v-show="allLoaded">已全部加载</span>
      </li>
    </ul>
  </div>
</template>
<script>
  import { Toast,InfiniteScroll} from 'mint-ui';
  export default {
    data(){
      return {
        list:[],
        moreLoading: false,
        queryLoading: false,
        allLoaded: false,
        pageSize: 20,
        pageNum: 1,
        totalNum: 10,
      }
    },
    filters:{
      typeFilter(val){
        if (val === 1){
          return '开通粉丝'
        } else if (val === 2){
          return '开通达人'
        } else {
          return '开通爱心天使'
        }
      },
      timeFilter(val){
        if (val){
          return new Date(val*1000).toLocaleDateString();
        }
      }
    },
    methods: {
      //下拉刷新
      loadMore(){
        if(this.allLoaded){
          this.moreLoading = true;
          return;
        }
        if(this.queryLoading){
          return;
        }
        this.moreLoading = !this.queryLoading;
        this.pageNum++;
        this.getRecord(this.pageNum);
      },
      getRecord(page = 1){
        this.post('home/User/getFuGouLog',{
          page: page,
          number: 20
        }).then(res => {
          if (res.data.code === 10000) {
            this.totalNum = res.data.info.number;
            this.list = this.list.concat(res.data.info.data);
            this.allLoaded = this.list.length=== this.totalNum;
            this.moreLoading = this.allLoaded;
          }
        })
      }
    },
    mounted(){
      this.getRecord();
    }
  }
</script>
<style scoped>
  @import "../../../static/recode.css";
</style>



