<template>
  <div class="ear">
    <header class="count_top header">
      <img src="../../assets/shopImage/left_icon.png" alt="" @click="back">
      <span>我的麦穗</span>
    </header>
    <ul class="nav">
      <li v-for="(item,i) in navs" :class="i===0 ? 'active_top': '' " class="nav_d" @click="setNav(i)">{{item.name}}</li>
    </ul>
    <div class="main">
      <share v-if="flag === 0"></share>
      <family v-else-if="flag === 1"></family>
      <chase v-else></chase>
    </div>
  </div>
</template>
<script>
  import share from './share'
  import family from './family'
  import chase from './chase'
  export  default {
    data (){
      return {
          navs: [
            {name: '爱心'},
            {name: '蜂巢'},
            {name: '蜂蜜'}
            ],
        flag: 0
      }
    },
    methods:{
      clearNav(){
        let navs = Array.from(document.querySelectorAll('.nav_d'));
        navs.forEach(item => {
          item.className = 'nav_d';
        })
      },
      setNav(index){
        this.flag = index;
        this.clearNav();
        let navs = Array.from(document.querySelectorAll('.nav_d'));
        navs[index].className = 'nav_d  active_top';
      },
      back(){
        this.$router.go(-1);
      }
    },
    components: {
      share,
      family,
      chase
    }
  }
</script>
<style scoped>
  @import "../../../static/header.css";
  .ear {
    height: 100%;
    background-color: #F2F2F2;
  }
  .nav {
    margin-top: 1px;
    display: flex;
    align-items: center;
    background-color: #FFFFFF;
  }
  .nav_d {
    flex: 1;
    height: 0.88rem;
    line-height: 0.88rem;
    text-align: center;
    font-size: 0.26rem;
    color: #111111;
  }
  .active_top {
    color: #ff6f00;
    border-bottom: 2px solid #ff6f00;
  }
  .main {
    height: calc(100vh - 1.8rem);
    overflow-y: auto;
  }
</style>



